/* eslint-disable prettier/prettier */
const ONEDAY = 12;
const NOTYET = 16;
const SOMEDAYS = 25;

const RefineTILData = (rawdata) => {
  const dataArrStartingWithAreaTags = rawdata
    .split('<area>')
    .filter((line) => line.includes('area'));

  const dataArrBetweenAreaTags = [];
  dataArrStartingWithAreaTags.forEach((element) => {
    dataArrBetweenAreaTags.push(
      element.substring(0, element.indexOf('</area>'))
    );
  });

  const myData = [];
  let startDay = 0;
  let endDay = 0;

  let howLongTake = 0;

  dataArrBetweenAreaTags.forEach((element) => {
    const lectureName = element.substring(2, element.indexOf('(2022') - 1);

    //  ,  
    //  : element.indexOf(')') - element.indexOf('(2022')
    // (1)   
    // ex. (2022-03-22)   = 11   (12 - 1)
    // (2)   
    // ex. (2022-03-25 ~ 2022-03-26)   = 24  (25 - 1)
    // (3)    
    // ex. (2022-04-14 ~ )     = 14
    howLongTake = element.indexOf(')') - element.indexOf('(2022');
    if (howLongTake < ONEDAY) {
      // (1)   
      startDay = element.substring(
        element.indexOf('(2022') + 1,
        element.indexOf(')')
      );
      endDay = startDay;

      myData.push({
        title: lectureName,
        start: startDay,
        end: endDay,
      });
    } else if (howLongTake < NOTYET) {
      // (2)    
      startDay = element.substring(
        element.indexOf('(2022') + 1,
        element.indexOf(' ~')
      );

      // endDay  
      const today = new Date();
      const month = today.getMonth() + 1 < 10
        ? `0${today.getMonth() + 1}`
        : today.getMonth();
      
      endDay = `${today.getFullYear()}-${month}-${today.getDate()}`;

      if((Number)(startDay[8]+startDay[9]) === today.getDate()){
        myData.push({
          title: lectureName,
          start: startDay,
          end: endDay,
        });
      } else {
        myData.push({
          title: lectureName,
          start: `${startDay}T00:00:01`,
          end: `${endDay}T23:00:00`
        });
      }
      
    } else if (howLongTake < SOMEDAYS) {
      // (3)   
      startDay = element.substring(
        element.indexOf('(2022') + 1,
        element.indexOf(' ~')
      );
      endDay = element.substring(
        element.indexOf(' ~') + 2,
        element.indexOf(')')
      );

      myData.push({
        title: lectureName,
        start: `${startDay}T00:00:01`,
        end: `${endDay}T22:00:00`,
      });
    }
  });

  return myData;
};

export default RefineTILData;
