import React from 'react';
// Components
import DrawCalendar from './DrawCalendar';
// StyledComponents
import StyMainFrame from '../styled/StyMainFrame';
// utils
import RefineTILData from '../utils/RefineTILData';
import tilRawDataQuery from '../utils/tilRawDataQuery';

function CalenderMain() {
  const { status, data, error } = tilRawDataQuery();

  if (status === 'loading') {
    return (
      <StyMainFrame>
        <DrawCalendar />
      </StyMainFrame>
    );
  }
  if (status === 'error') {
    return <div>{error.message}</div>;
  }
  return (
    <StyMainFrame>
      <DrawCalendar calendarInputData={RefineTILData(data)} />
    </StyMainFrame>
  );
}
export default CalenderMain;
