import axios from 'axios';
import { useQuery } from 'react-query';

const fetchTILReadme = async () => {
  const { data } = await axios.get(
    'https://raw.githubusercontent.com/PhysicksKim/TIL/main/README.md'
  );
  return data;
};

const tilRawDataQuery = () => {
  const { status, data, error } = useQuery('tilraw', fetchTILReadme);
  return { status, data, error };
};

export default tilRawDataQuery;
