/* eslint-disable react/prop-types */
/* eslint-disable react/destructuring-assignment */
import React from 'react';

// Calendar Package
import FullCalendar from '@fullcalendar/react'; // must go before plugins
import dayGridPlugin from '@fullcalendar/daygrid'; // a plugin!

// Tooltip Package
import ReactTooltip from 'react-tooltip';
// Tooltip texts are created here
import injectTooltipText from '../utils/injectTooltipText';

export default function DrawCalendar(props) {
  // Tooltips were drawn by <ReactToolTip /> tag.
  // If ReatTooltip.rebuild() run, it updates new targets to show tooltips.
  return (
    <>
      <ReactTooltip />
      <FullCalendar
        plugins={[dayGridPlugin]}
        initialView="dayGridMonth"
        events={props.calendarInputData}
        contentHeight="auto"
        eventContent={injectTooltipText}
        stickyHeaderDates
        headerToolbar={{ left: 'title' }}
        eventDidMount={() => {
          ReactTooltip.rebuild();
        }}
      />
    </>
  );
}
